import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';

const ImgMediaCard = ({cards}) => {
    const promociones = [];
    for (let clave in cards){
        console.log(clave, cards[clave])
        promociones.push(cards[clave])
    }

    function replaceSpaces(inputStr) {
        // Usamos la función replace de JavaScript para reemplazar los espacios con '%20'
        let link = "https://api.whatsapp.com/send?phone=5492604632416&text=Hola!%20Estoy%20Interesado%20en%20la%20promo:%20"
        let outputStr = inputStr.replace(/ /g, '%20');
        link += outputStr
        return link;
      }


  return (
    <div className='d-flex flex-row justify-content-center contenedorCollage'>
        {promociones.map((card)=>(
            <div className="card col mx-3">
                <img src={card.img} className="card-img-top" alt="..."/>
                <div className="card-body">
                    <h5 class="card-title">{card.title}</h5>
                    <p class="card-text">{card.description}</p>
                    <a target="_blank" rel="noreferrer" href={replaceSpaces(card.title)} type='button' className='btn btn-success' size="small">Contacto</a>
                </div>
            </div>
        ))}
    </div>
  );
}

export default ImgMediaCard